.App {
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  min-height: 100vh;
  padding: 2rem;
  transition: background-color 0.4s ease, color 0.4s ease;
}

.App.dark {
  background-color: #121212;
  color: #f1f1f1;
}

.App.light {
  background-color: #f2f2f2;
  color: #121212;
}

.toggle-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: #333;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
  z-index: 1000;
}

.toggle-btn:hover {
  background-color: #555;
}

.todo-box {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  width: 400px;
  margin: 100px auto;
  backdrop-filter: blur(10px);
  transition: background-color 0.4s ease;
}

.App.light .todo-box {
  background-color: rgba(255, 255, 255, 0.9);
}

h1 {
  margin-bottom: 1.5rem;
}

input {
  padding: 0.5rem 1rem;
  width: 70%;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-right: 10px;
}

button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 8px;
  background-color: #bb86fc;
  color: black;
  cursor: pointer;
}

button:hover {
  background-color: #985eff;
}

ul {
  list-style-type: none;
  padding: 0;
  margin-top: 2rem;
}

li {
  background-color: #1e1e1e;
  padding: 1rem;
  border-radius: 8px;
  margin: 10px auto;
  text-align: left;
  color: #f1f1f1;
}

.App.light li {
  background-color: #ffffff;
  color: #333;
}

.delete-btn {
  margin-left: 10px;
  background: transparent;
  border: none;
  color: red;
  font-size: 1.2rem;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.delete-btn:hover {
  transform: scale(1.2);
}
/* Light Mode Styles (default) */
.App {
  background-color: #fff;
  color: #000;
  padding: 20px;
}

/* Dark Mode Styles */
.dark-mode {
  background-color: #121212;
  color: white;
}

button {
  background-color: #bb86fc;
  color: #000;
}

button.toggle-btn {
  background-color: #6200ea;
  color: white;
  padding: 10px;
  margin-top: 20px;
  border-radius: 5px;
}
.todo-container {
  max-height: 300px; /* adjust as per your design */
  overflow-y: auto;
  padding: 10px;
  margin-top: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.05); /* light transparent bg */
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
}
.todo-container::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 100%;
  background-color: #888;
  left: 15px;
  top: 0;
}
.todo-container {
  position: relative;
  padding-left: 30px;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

li {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  padding: 8px;
  border-bottom: 1px dashed #888;
  font-family: 'Courier New', monospace;
}
.toggle-mode {
  padding: 8px 16px;
  border-radius: 20px;
  cursor: pointer;
  border: none;
  background-color: #444;
  color: white;
  transition: all 0.3s ease;
}
li {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.9); }
  to { opacity: 1; transform: scale(1); }
}
body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  transition: all 0.3s ease-in-out;
}

:root {
  --bg-color: #f7f4ea;
  --text-color: #2e2e2e;
  --box-bg: #fff8dc;
}

.dark-mode {
  --bg-color: #1e1e1e;
  --text-color: #f7f4ea;
  --box-bg: #2e2e2e;
}

.container {
  max-width: 500px;
  margin: 60px auto;
  padding: 25px;
  background: var(--box-bg);
  border-radius: 20px;
  box-shadow: 0 0 30px rgba(0,0,0,0.1);
  max-height: 500px;
  overflow-y: auto;
  transition: 0.4s ease-in-out;
}

h1 {
  text-align: center;
  font-family: 'Caveat', cursive;
  font-size: 2.5rem;
}

input[type="text"] {
  width: 70%;
  padding: 10px;
  border-radius: 12px;
  border: 1px solid #ccc;
  font-size: 1rem;
  margin-right: 10px;
}

button {
  padding: 10px 16px;
  border: none;
  border-radius: 12px;
  background-color: #6c63ff;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s ease;
}

button:hover {
  background-color: #5a54d1;
}

.task {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  border-bottom: 1px dashed #aaa;
  font-size: 1.1rem;
  transition: 0.2s ease-in-out;
}

.task:hover {
  background-color: rgba(0,0,0,0.05);
  border-radius: 10px;
}
